body {
  margin: 0px;
  padding : 0px;
}

* {
  box-sizing: border-box;
}

#root {
  height: 100vh;
  width: 100vw;
  border: 2px solid red;
  display: flex;
  flex-direction: column;
}

.add-task {
  height: 10vh;
  width: 100vw;
  border: 2px solid blue;
}

.add-task form {
  height: 100%;
  width: 30%;
  margin: 0px auto;
  padding: 10px;
}

.add-task form input {
  margin: 0px 10px;
  padding: 10px;
  border-radius: 5px;
}

.add-task form input[type=button] {
  cursor: pointer;
}

.add-task form input[type=button]:hover {
  background-color: antiquewhite;
}

.task-lists {
  height: 90vh;
  width: 100vw;
  border: 2px solid green;
  display: flex;
  flex-direction: row;
}

.task-lists .todo {
  height: 100%;
  width: 50%;
  border: 2px solid black;
}

.task-lists .finished {
  height: 100%;
  width: 50%;
  border: 2px solid black;
}

.task-lists .list-container {
  height: 70%;
  width: 80%;
  background-color: aquamarine;
  margin: 50px auto;
  border-radius: 5px;
}

.task-lists .title {
  height: 10%;
  width: 100%;
  background-color: teal;
  color: white;
  text-align: center;
  font-size: 30px;
  padding: 5px;
  border-radius: 5px;
}

.task-lists .list-container .content{
  height: 90%;
  width: 100%;
  background-color: beige;
  font-size: 17px;
  overflow-y: auto;  /*it shows scrollbar if list increases */
}

.task-lists .list-container .content div{
  height: 50px;
  padding-top: 10px;
  text-align: center;
  background-color: aquamarine;
  border-bottom: 1px solid teal;
}

.task-lists .list-container .content div span {
  margin: 0px 20px;
}

.task-lists .list-container .content div span:hover {
  color: white;
  cursor: pointer;
}

